import React from 'react';
import {NavLink} from "react-router-dom";
import c from "./navbar.module.css"

const Navbar = () => {
  return (
    <nav className={c.nav}>
      
    <div className={c.item}>
        <NavLink to="/about" activeClassName={c.activeLink}>About</NavLink>
    </div>
    <div className={`${c.item} ${c.active}`}>
        <NavLink to="/technologies" activeClassName={c.activeLink}>Technologies</NavLink>
    </div>
    <div className={`${c.item} ${c.active}`}>
        <NavLink to="/portfolio" activeClassName={c.activeLink}>Portfolio</NavLink>
    </div>
    <div className={`${c.item} ${c.active}`}>
        <NavLink to="/contact" activeClassName={c.activeLink}>Contact</NavLink>
    </div>

 
</nav>
      )
}

export default Navbar;